/* webfont import */
@font-face {
  font-family: 'Cafe24Ssurround';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Pretendard-Light';
  src: url('./fonts/Pretendard-ExtraLight.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('./fonts/Pretendard-Regular.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-semiBold';
  src: url('./fonts/Pretendard-SemiBold.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-Bold';
  src: url('./fonts/Pretendard-ExtraBold.woff') format('woff');
}





*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html{
  min-height:100%;
  background-color: #e3e6eb;
}

p{
  margin-bottom:0;
}

.App {
  text-align: center;
  background-color: #e3e6eb;
}

/* Header style */
.App-header {
  background-color: #e3e6eb;
  /* width:90%; */
}
.navBarPC{
  /* border:1px solid red; */
  background-color: #e3e6eb;
  width:90%;
  margin: 30px auto;
  font-family: 'Cafe24Ssurround', 'Courier New', monospace;
  font-stretch:extra-expanded;
}
.navContainer{

}
.logo{
  width:90px;
  display: inline-block;

  /* drag prevention */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  /* user-drag: none; */
}
.navBoxPC{
  margin-right:30px;
}
.navItem{
  color:#8f8f8f;
}
.home, .project, .contact{
  margin-right:7px;
}
.navItem:hover{
  color:#405980;
}

/* main style*/
.main{
  width: 90%;
  max-width: 1300px;
  height:auto;
  /* border:1px solid blue; */
  background-color: #f9f9f9;
  margin: 120px auto 50px;
  padding: 50px;
  position:relative;
  border-radius: 15px;
  transition: all 2s;

  font-family: 'Pretendard-Regular', 'Courier New', monospace;
  color:#676767;
}
.circleProfile{
  width:330px;
  height:330px;
  /* border:1px solid purple; */
  border-radius: 50%;
  position:absolute;
  top:-100px;
  left:5%;
  border:15px solid white;
  /* box-shadow: 10px 10px 1.5px #888e98; */
  transition: all 0.3s;
}
.profilePic{
  width:100%;
  height:100%;
  border-radius: 50%;

  /* drag prevention */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

/* Profile Contents Style */
.profileContainer{
  /* border:1px solid red; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: minmax(150px, auto);
  column-gap: 5px;
  row-gap: 15px;
  width:100%;
  height:auto;
  padding:20px;
}
.profileBox{
  /* border:1px solid blue; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.profileBox > p{
  margin-bottom:10px;
  
}


.box1{
  grid-row: 1/3;
  justify-content: flex-start;
}
.meBox{
  /* border:1px solid mediumaquamarine; */
  width:70%;
  height:auto;
  margin: auto;
  margin-top:200px;
  text-align: left;
}
.meBox > p{
  font-size:90%;
  margin-bottom:6px;
}
.meMail{
  text-decoration: none;
  color:#5f8acb;
  transition: all 0.3s ease-in-out;
}
.linkIcon{
  display: block;
  margin:15px auto;
  color:#b0b0b0;
}
.meA{
  display: block;
  width:fit-content;
  color:#8ac4e6;
  transition: all 0.3s ease-in-out;
  font-size: 75%;
  margin-bottom:5px;
}
.meBtnBox{
  margin:auto;
  width:fit-content;
  margin-top:30px;
}
.meBtn{
  display: inline-block;
  font-size:77%;
  text-decoration: none;
  background-color: #84a4c5;
  color:white;
  padding: 5px 14px;
  border-radius: 15px;
  margin: 7px;
  transition: all 0.3s ease-in-out;
  text-align: center;
}
.meBtn:hover{
  background-color: #b0c7e0;
  color:white;
}

.box2{
  border-right: 1px solid #929292;
}
.icon{
  display: inline-block;
  margin-right:10px;
  color:#445671;
}
.gpa{
  font-size:0.75em;
  color:#929292;
}

.box3{
  text-align: left;
  padding-left: 40px;
  font-size:14.5px;
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  line-height: 20px;
  letter-spacing: -0.1px;
}

.box4{
  grid-column: 2/4;
  /* border-top:1px solid #929292; */
  padding: 12px 8px;
}
.expBox{
  font-family: 'Pretendard-Bold', 'Courier New', monospace;
  font-size: 18px;
  color:#556a8a;
  text-transform: uppercase;
}
.expLine{
  display: block;
  width:1px;
  height:20px;
  border-left:1.5px solid #929292;
  margin:5px auto;
}
.jobBox{
  margin: 10px auto;
  /* border:1px solid purple; */
  width:90%;
}
.jobTitle{
  font-family: 'Pretendard-semiBold', 'Courier New', monospace;
  display: inline-block;
  font-size: 18px;
  color:#5d6d87;
  
}
.jobCircleBox{
  display: inline-block;
  color:#929292;
  text-shadow: none;
}
.jobCircle{
  display: inline-block;
  width:6px;
  height:6px;
  border-radius: 50%;
  margin:0 5px 2px 10px;
}
.red{
  background-color: #bc4044;
}
.green{
  background-color: #679261;
}
.jobType{
  display: inline-block;
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  font-size:12.5px;
}
.jobLocation{
  font-size:15px;
}
.jobDuration{
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  font-size:13.5px;
}

.box5{
  grid-column: 1/4;
  margin-top:25px;
  text-align: center;
}
.skBox{
  font-family: 'Pretendard-Bold', 'Courier New', monospace;
  font-size: 18px;
  color:#556a8a;
  text-transform: uppercase;
}
.skLine{
  display: block;
  width:100px;
  height:1px;
  border-bottom:1px solid #929292;
  margin:5px auto;
}
.skCircleBox{
  /* border:1px solid #467ed2; */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width:80%;
  margin: 25px auto;
}
.skCircle{
  /* width:150px; */
  /* height:150px; */
  font-family: 'Pretendard-Regular', 'Courier New', monospace;
  position: relative;
  /* border:1px solid red; */
  margin: 15px 25px;
}
.indicator{
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  font-size:90%;
}
.indicator>p:first-child{
  margin-bottom:5px;
}


@media (max-width:1200px){
  .profileBox{
    margin-bottom:25px;
  }
  .box1{
    
  }
  .meBox{
    width:90%;
    margin-top: 180px;
  }
  .meBtn{
    display: block;
    margin: 15px 0;
  }

  .box2{
    grid-column: 2/4;
    border-right: none;
  }
  .box3{
    grid-column: 2/4;
    grid-row: 2/3;
    text-align: center;
    border-left: 1px solid #929292;
    padding-left: 0;
  }
  .box4{
    grid-column: 1/4;
  }
  .expLine{
    width:110px;
    height:1px;
    border-left:none;
    border-bottom:1px solid #929292;
    margin:5px auto;
  }
  .box5{
    margin-top:8px;
    margin-bottom:0px;
  }
}

@media (max-width: 868px){
  .circleProfile{
    width:280px;
    height:280px;
  }
  .meBox{
    width:100%;
    margin-top:120px;
  }
  .meBox > p {
    font-size:70%;
  }
  .meA{
    font-size:65%;
  }
  .meBtnBox{
    margin-top:20px;
  }
  .meBtn{
    font-size:70%;
  }

  .box2{
    text-align: right;
    padding-right:30px;
    font-size:95%;
  }
  .box3{
    font-size:88%;
  }
  .jobTitle{
    font-size: 95%;
  }
  .jobLocation, .jobDuration{
    font-size:90%;
  }
  .box5{
    margin-bottom:0px;
  }


  
}


/* Media Query for Smaller Devides */
@media (max-width: 668px){

  .main{
    width:95%;
    margin-bottom: 150px;
  }
  .circleProfile{
    width:300px;
    height:300px;
    left:50%;
    transform: translate(-50%, 0);
  }

  .profileContainer{
    display: block;
    padding-bottom: 0;
  }
  .profileBox{
    margin: 45px auto;
    min-height:150px;
  }
  .box1{
    margin-top:150px;
  }
  .meBox{
    margin-top:10px;
    text-align: center;
  }
  .meBox > p{
    font-size:90%;
  }
  .meA{
    font-size:80%;
    margin: 7px auto;
  }
  .meBtn{
    font-size: 80%;
  }

  .box2{
      text-align: center;
      padding-right: 0px;
  }
  .box3{
    border-left:none;
  }
  .box4{
    border-top:none;
  }
  .jobCircleBox{
    display: block;
    font-size:60%;
    margin:8px 0 0 0;
  }
  .jobLocation, .jobDuration{
    font-size:70%;
  }
  .box5{
    margin-bottom:0px;
  }

}





